<template>
	<div id="whole-login">
		<div>
			<h1 @click="userLogin()">登录</h1>
			<a-form id="components-form-demo-normal-login" :form="form" class="login-form" @submit="handleSubmit">
				<a-form-item>
					<a-input v-decorator="[
		          'userName',
		          { rules: [{ required: true, message: 'Please input your username!' }] },
		        ]"
					 placeholder="Username">
						<a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
					</a-input>
				</a-form-item>
				<a-form-item style="margin-top: -10px;">
					<a-input v-decorator="[
		          'password',
		          { rules: [{ required: true, message: 'Please input your Password!' }] },
		        ]"
					 type="password" placeholder="Password">
						<a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
					</a-input>
				</a-form-item>
				<a-form-item style="margin: -20px 0 -5px 0;">
					<a-checkbox v-decorator="[
					  'admin',
					  {
					    valuePropName: 'checked',
					    initialValue: false,
					  },
					]" >
						Admin Access
					</a-checkbox>
				</a-form-item>
				<a-form-item>
					<a-checkbox v-decorator="[
		          'remember',
		          {
		            valuePropName: 'checked',
		            initialValue: true,
		          },
		        ]">
						Remember me
					</a-checkbox>
					<a class="login-form-forgot" href="">
						Forgot password
					</a>

					<a-button style="margin-top: 15px;" type="primary" html-type="submit" class="login-form-button">
						Log in
					</a-button>
					Or
					<router-view></router-view>
				</a-form-item>
			</a-form>
		</div>
	</div>

</template>

<script>
	import {
		mapMutations,
		mapState
	} from "vuex";
	export default {
		data() {
			return {
				user: {
					"name": "whx",
					"password": "123",
				},
			}
		},
		beforeCreate() {
			this.form = this.$form.createForm(this, {
				name: 'normal_login'
			});
		},
		computed: {
			...mapState(['requestPath'])
		},
		methods: {
			...mapMutations(["login"]),
			async userLogin(isAdmim) {
				let parm = 'userLogin/'
				if(isAdmim){
					parm = 'adminLogin/'
				}
				let {
					data
				} = await this.axios.post(this.requestPath + parm, this.user);
				if (data.code == 0) {
					this.$message.success(' (｡･∀･)ﾉﾞ 登录成功~ ', 1);
					this.login(data.data);
					if(!isAdmim){
						this.$router.push({
							path: '/home'
						});
					}else{
						this.$router.push({
							path: '/admin'
						});
					}

				} else {
					this.$message.error(' (￣△￣；) 账号名或密码错误~ ', 2);
				}
			},
			async handleSubmit(e) {
				e.preventDefault();
				this.form.validateFields((err, values) => {
					if (!err) {
						this.user.name = values.userName;
						this.user.password = values.password;
						this.userLogin(values.admin);
					}
				});
			},
		}
	}
</script>

<style lang='scss'>
	#whole-login {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100vh;
		background-image: url('/login_bg.jpg');
		background-repeat: no-repeat;
		background-size: 100% 100%;
	}

	#components-form-demo-normal-login {
		.login-form {
			max-width: 300px;
		}

		.login-form-forgot {
			float: right;
		}

		.login-form-button {
			width: 100%;
		}
	}
</style>
